import {
    mapMutations
} from 'vuex';
export default {
	data() {
		return {
			showPassword: true,
			loginInfo: {
				mobile: "",
				password: ""
			},
			path:"",
		}
	},
	onLoad(options) {
		let that = this;
		that.path = that.$Route.query.path;
	},
	methods: {
		changePassword() {
			let that = this;
			that.showPassword = !that.showPassword;
		},
		clearIcon() {
			let that = this;
			that.loginInfo.mobile = "";
		},
		toLogin() {
			let that = this;
			let mobile = that.loginInfo.mobile;
			let password = that.loginInfo.password;
			if (!that.$utils.common.phoneTest.test(mobile)) {
				uni.showToast({
					title: "请输入正确的手机号",
					icon: "none"
				})
			} else if (password.length < 6) {
				uni.showToast({
					title: "密码需六位以上字符",
					icon: "none"
				})
			} else {
					uni.showLoading({
						title:"登录中..."
					})
					that.$api.login(that.loginInfo).then(res => {
						uni.hideLoading();
						if(res.success){
							var storageData={
								accessToken:res.datas.accessToken
							}
							that.login(storageData);
							uni.showToast({
								title: "登录成功",
								icon: "success"
							})
							setTimeout(()=>{
								that.goback();
							},400)
							
						}
						else{
							uni.showToast({
								title: res.message,
								icon: "none"
							})
						}
						
					})
				
			}
		},
		goback(){
			const that = this;
			if(that.path){
					var path = decodeURIComponent(that.path);
					this.$Router.replace({
						path:decodeURIComponent(that.path).split("?")[0],
						query:that.$utils.getParameter(path)
					})
			}
			else{
				uni.switchTab({
				    url: '/pages/my/index',
				    success: function(e) {






				       var page = getCurrentPages()[0]
				       if (page == undefined || page == null) return;
				       page.onLoad()

				    }
				});
				// this.$Router.pushTab({
				// 	path:"/pages/my/index"
				// })
			}
		},
		forgetPassword(){
			let that = this;
			that.$Router.push({
				path:"/pages/my/forgetpwd"
			})
		},
		gotoAgreement1(){
			let that = this;
			that.$Router.push({
				path:"/pages/my/agreement/agreement1"
			})
		},	
		gotoAgreement2(){
			let that = this;
			that.$Router.push({
				path:"/pages/my/agreement/agreement2"
			})
		},	
		register(){
			let that = this;
			that.$Router.push({
				path:"/pages/my/register"
			})
		},
		  ...mapMutations(['login'])
	}
}
